@import '../../default-style/params.scss';

.comment-container {
    display: flex;

    margin-bottom: 24px;

    &:last-child {
        margin-bottom: 0;
    }

    &:first-child {
        margin-top: 24px;
    }

    .user-avatar {
        width: 50px;
        height: 50px;
        border-radius: 50%;

        flex: none;


        background: {
            repeat: no-repeat;
            position: center;
            size: cover;
        }

        border: 2px solid #000000;

        &.default {
            background-image: url('./img/default-user-logo.svg');
            background-size: 40px 40px;
            background-position: bottom;
        }
    }

    .comment-inner {
        flex: 1 1 auto;

        margin-left: 12px;

        padding: 4px 0;


        div {
            font-family: $font-2;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 18px;

            color: $black;
        }

        .author-info {
            display: flex;
            justify-content: space-between;
            align-items: center;

            span {
                font-family: $font-1;

                font-weight: bold;
                font-size: 16px;
                line-height: 108%;

                color: $black;
            }

            .names {
                display: flex;

                .name-and-surname {
                    margin-right: 10px;
                }

                .username {

                    span {
                        opacity: 0.25;

                        transition: all ease .1s;

                        &:hover {
                            opacity: 1;
                        }
                    }
                }
            }

            .comment-context-menu-open-btn {
                cursor: pointer;

                width: 4px;
                height: 17px;

                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;

                background-image: url('./img/menu.svg');

                transition: all ease .15s;

                &:hover {
                    filter: brightness(0);
                }
            }
        }

        .my-comment {
            margin: 8px 0px 16px;

            padding-bottom: 4px;
            border-bottom: 2px solid $black;

            word-break: break-all;
        }

        .user-comment {
            max-height: 216px;
            overflow: hidden;

            position: relative;

            .comment-text {
                word-break: break-all;
            }

            &:after {
                content: '';

                width: 100%;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;
                z-index: 99;

                background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);

                display: block;
            }

            .all-comment-btn {
                position: absolute;
                cursor: pointer;

                left: 0;
                right: 0;
                top: calc(100% - 25px);

                margin: auto;

                z-index: 999;

                display: flex;
                justify-content: center;

                span {

                    font-size: 20px;
                    line-height: 23px;
                    text-align: center;
                    font-weight: bold;

                    color: #2883EE;
                }
            }

            &.hidden {
                &:after {
                    display: none;
                }

                .all-comment-btn {
                    display: none;
                }
            }

            &.open {
                max-height: none;

                &:after {
                    display: none;
                }

                .all-comment-btn {
                    display: none;
                }
            }
        }

        .little-blue-btn {
            display: flex;
            justify-content: flex-end;

            button {
                padding: 8px 30px;

                span {
                    font-size: 16px;
                }
            }
        }

        .comment-text {
            margin: 8px 0;
        }

        .bottom {
            border: none;
            padding: 0;

            .left {
                .likes {
                    &.active {
                        &::before {
                            background-image: url('./img/active-like.svg');
                        }
                    }
                }
            }
        }
    }
}