%quote-field {
    padding: 20px 24px 20px 64px;
    font-style: italic;

    &:before {
        pointer-events: none;
        position: absolute;
        left: 24px;
        top: 24px;

        content: '“';
        width: 24px;
        height: 24px;

        font-size: 64px;
        line-height: 100%;

        font-style: normal;
    }
}