.stories-container {
    width: 100%;

    padding: 24px 0;

    flex: 1 1 auto;

    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;

    .story-preview__container {
        margin-bottom: 24px;
        width: 100%;

        &:last-child {
            margin-bottom: 0;
        }

        .story-preview {
            width: 100%;
        }

        @media (min-width: 800px) {
            width: 50%;

            &:nth-child(2n) {
                padding-left: 12px;
            }

            &:nth-child(2n-1) {
                padding-right: 12px;
            }

            &:nth-last-child(2) {
                margin-bottom: 0;
            }
        }
    }
}