@import '../text-field/style.scss';
@import '../quote-field/style.scss';
@import '../distributor/style.scss';
@import '../image-field/style.scss';

@import '../../../../default-style/params.scss';

%stories-block {
    width: 100%;

    position: relative;

    &.with-border {
        border: 2px solid $black;
        border-radius: 10px;
        overflow: hidden;

        &.stretch {
            flex:  1 1 auto;
        }
    }

    &.show-menu {

        &:hover {
            .menu {
                display: flex;
            }
        }

        &:active {
            .menu {
                display: flex;
            }
        }
    }

    &.mobile {
        position: fixed;
        cursor: grabbing;

        .menu {
            .move {
                cursor: grabbing!important;
            }
        }

        z-index: 999;
    }

    .menu {
        position: absolute;
        right: 0;
        top: 0;
        background: $white;

        display: flex;
        justify-content: center;
        align-items: center;

        width: 68px;
        height: 40px;

        $border: 2px solid $black;

        border: {
            bottom: $border;
            left: $border;
        }

        border-radius: 0px 10px;

        z-index: 99;

        display: none;

        .menu-item {
            width: 16px;
            height: 22px;

            background: {
                position: center;
                repeat: no-repeat;
                size: contain;
            }

            ;

            border-radius: 4px;

            &:hover {
                background-color: $grey;
            }

            &:first-child {
                margin-right: 12px;
            }

            &.move {
                background-image: url('./img/move.svg');
                cursor: grab;
            }

            &.basket {
                background-image: url('./img/basket.svg');
            }
        }
    }

    &.distributor-menu {
        .menu {
            bottom: 0;
            margin: auto;
            border: 2px solid $black;
            border-radius: 12px;
        }
    }

    * {
        font-family: $font-1;
        color: $black;


        font-size: 24px;
        line-height: 36px;
    }

    .field {
        width: 100%;
        height: 100%;
    }

    .text {
        @extend %text-field;
    }

    .quote {
        @extend %quote-field;
    }

    .quotes {
        position: absolute;
        pointer-events: none;

        &.top {
            left: 0;
            top: 0;

            margin-left: 12px;
            margin-top: 10px;
        }

        &.bottom {
            right: 0;
            bottom: 0;

            margin-right: 12px;
            margin-bottom: 2px;
        }
    }

    .distributor {
        @extend %distributor;
        margin: 12px 0;
    }

    .image-field {
        @extend %image-field;
    }

    .text,
    .quote,
    .image-field {
        background: $white;
    }
}