@import '../../../default-style/params.scss';

%add-field-buttons-group {
    display: flex;
    justify-content: center;
    align-items: center;

    .add-field {
        cursor: pointer;
        font-family: $font-2;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        width: 109px;
        height: 74px;

        margin-right: 24px;

        &:last-child {
            margin-right: 0;
        }

        span {
            font-size: 16px;
            line-height: 18px;
            text-align: center;

            color: $black;
            opacity: .6;

            transition: all ease .15s;
        }

        &:before {
            content: '';

            width: 50px;
            height: 50px;

            border: 1px solid $dark-grey;
            border-radius: 5px;

            background: {
                position: center;
                repeat: no-repeat;
            }

            transition: all ease .2s;
        }

        &.add-text-field::before {
            background-image: url('./img/text.svg');
        }

        &.add-quote-field::before {
            background-image: url('./img/quote.svg');
        }

        &.add-img-field {
            input {
                display: none;
            }

            &::before {
                background-image: url('./img/gallery.svg');
            }
        }

        &.add-distributor::before {
            background-image: url('./img/arrow.svg');
        }

        &:hover {
            span {
                opacity: 1;
            }

            &::before {
                border-color: $black;

                filter: brightness(0);
            }
        }
    }

    @media (max-width: 550px) {
        justify-content: space-evenly;

        .add-field {
            height: auto;
            width: auto;

            span {
                display: none;
            }
        }
    }
}