@import './params.scss';

//
//scroll
//
::-webkit-scrollbar {
    width: 12px;
    background: $black;
}

::-webkit-scrollbar-thumb {
    background-color: $white;
    border: 2px solid $black;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background-color: $grey;
}

//
//selection
//
::selection {
    background: $black;
    color: $white;
}

//
//default html styles
//
* {
    box-sizing: border-box;
    color: $black;
}

html {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;

    background: $white;
    height: 100%;
    overflow-x: hidden;
}

#root {
    position: relative;
    display: flex;
    flex-direction: column;

    margin: 0 $tablet-padding;

    height: 100%;

    @media (max-width: 1000px) {
        margin: 0 $mobile-padding;
    }
}

//--------------------------------------------
header,
footer {
    flex: none;
}

main {
    flex: 1 1 auto;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

//--------------------------------------------
ul,
li {
    margin: 0;
    padding: 0;

    list-style: none;
}

//--------------------------------------------
h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
aside,
pre,
blockquote,
q,
input,
button,
a {
    margin: 0;
    padding: 0;

    font-style: normal;
    font-weight: normal;
}

h1,
h2,
h3 {
    font-weight: bold;
    line-height: 108%;
    text-align: center;

    font-family: $font-1;
}

h1 {
    font-size: 90px;
}

h2 {
    font-size: 80px;
}

h3 {
    font-size: 36px;
}

a {
    text-decoration: none;
}

p,
blockquote {
    font-family: $font-1;
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
}

blockquote {
    font-style: italic;
}

span,
input,
button,
a,
aside {
    font-family: $font-2;
}

input,
button {
    border: none;
    background: none;
    outline: none;
}

input::placeholder {
    color: $black;
    opacity: .6;
}

button {
    cursor: pointer;
}

.storyteller-logo {
    background: {
        repeat: no-repeat;
    }

    width: 190px;
    height: 40px;
}